import React,{useState} from 'react';
const Counter = () => {
  const [value,setValue] = useState(0)

  const increase = ()=>{
   setValue(index => index + 1)
  }

  const decrease =()=>{
    setValue(index => {
      let newValue = index - 1;
      if(newValue < 0){
        return 0;
      }
      return newValue;
    })
  }

  const reset = ()=>{
    setValue(0)
  }

  return ( <section className='section-counter'>
    <div className='counter-container'>
      <h1 className='counter-value'>{value}</h1>
      <div className="btn-container">
        <button className='counter-btn' onClick={decrease}>decrease</button>
        <button className='counter-btn' onClick={reset}>reset</button>
        <button className='counter-btn' onClick={increase}>increase</button>
      </div>
    </div>
 
  </section> );
}
 
export default Counter;